st class hospitality\n            services, Boston Partners endeavors to become a leading resort and\n            spa operator in the continent, Hence the soon to be started\n            Kuriftu Resort and Spa Moucha Island.\n          </p></div></div></div></section> <section class=\"who-we-are\" data-v-5d3ea396><div class=\"container\" data-v-5d3ea396><div class=\"who-we-are-all\" data-v-5d3ea396><div class=\"stay-slide\" data-v-5d3ea396><div class=\"slide-card\" data-v-5d3ea396><div class=\"logo-over\" data-v-5d3ea396></div></div></div> <div class=\"stay-desc\" data-v-5d3ea396><h4 data-v-5d3ea396>who we are</h4> <p data-v-5d3ea396>\n            Boston Partners PLC, parent company of Kuriftu Resort, was\n            established in 2002 and is the leading, internationally renowned,\n            hospitality group based in Ethiopia, owning and operating\n            innovative brands across East Africa. The latest initiative of\n            Boston Partners PLC is to spearhead a promotional campaign to\n            promote Inter-African Tourism with a focus on fostering a new\n            mindset for the travel market.\n          </p> <img"+(_vm._ssrAttr("src",require("../assets/img/What Makes us unique.jpg")))+" alt class=\"mid-img\" data-v-5d3ea396> <h4 data-v-5d3ea396>WHAT MAKES US UNIQUE?</h4> <p class=\"unique-list\" data-v-5d3ea396><b data-v-5d3ea396> We </b> are a company solely owned and operated by\n            Africans.<br data-v-5d3ea396> <b data-v-5d3ea396> We </b> provide permanent employment opportunities to 2000+\n            people thus far.<br data-v-5d3ea396> <b data-v-5d3ea396> We </b> support local businesses by purchasing from the\n            communities that neighbor our resorts.<br data-v-5d3ea396> <b data-v-5d3ea396> We </b>are a Strategic Partner of Ethiopian Airlines and\n            Ethiopias Tourism Organization.<br data-v-5d3ea396> <b data-v-5d3ea396> We </b>produce all furniture in-house as well as other\n            culturally artisanal design elements of our resorts (Thatched\n            Roof, Woodwork, Leather Braiding, Stonework etc.).<br data-v-5d3ea396> <b data-v-5d3ea396> We </b>design all of our resorts in-house and construct them\n            with our own construction team.<br data-v-5d3ea396> <b data-v-5d3ea396> Our </b> CEO is an active member of the Ethiopian Board of\n            Tourism.<br data-v-5d3ea396> <b data-v-5d3ea396> Our </b> CEO is an active member of the Ethiopian Board of\n            Tourism.<br data-v-5d3ea396> <b data-v-5d3ea396> Our </b>78% is corporate team.<br data-v-5d3ea396></p></div></div> <div class=\"corporate\" data-v-5d3ea396></div> <div data-v-5d3ea396><div class=\"wrapper\" data-v-5d3ea396><div class=\"all-wrapper\" data-v-5d3ea396><div class=\"counter\" data-v-5d3ea396><div class=\"persent-wrapper\" data-v-5d3ea396><h2 data-to=\"71\" data-speed=\"10000\" class=\"timer count-title count-number\" data-v-5d3ea396></h2> <h4 data-v-5d3ea396>%</h4></div> <p class=\"count-text\" data-v-5d3ea396>Our line staff are females</p></div> <div class=\"counter\" data-v-5d3ea396><div class=\"persent-wrapper\" data-v-5d3ea396><h2 data-to=\"62\" data-speed=\"10000\" class=\"timer count-title count-number\" data-v-5d3ea396></h2> <h4 data-v-5d3ea396>%</h4></div> <p class=\"count-text\" data-v-5d3ea396>\n                Staff Emerge from Our Construction Team\n              </p></div> <div class=\"counter\" data-v-5d3ea396><div class=\"persent-wrapper\" data-v-5d3ea396><h2 data-to=\"80\" data-speed=\"10000\" class=\"timer count-title count-number\" data-v-5d3ea396></h2> <h4 data-v-5d3ea396>%</h4></div> <p class=\"count-text\" data-v-5d3ea396>\n                Hiring is Done Within the Local Community\n              </p></div> <div class=\"counter\" data-v-5d3ea396><div class=\"persent-wrapper\" data-v-5d3ea396><h2 data-to=\"98\" data-speed=\"10000\" class=\"timer count-title count-number\" data-v-5d3ea396></h2> <h4 data-v-5d3ea396>%</h4></div> <p class=\"count-text\" data-v-5d